import baseUrl from '../../utils/utils.js'
export default {
	data() {
		return {
			productList:[],
			totalPrice:0,
			allChecked:false,
			rec_id_str:'',
			total_num:0
		}
	},
	methods: {
		// 去逛逛
		goAround(){
			// console.log(1)
			wx.switchTab({
				url:"/pages/news/news"
			})
		},
		// 单选
		check(e){
			// console.log(e.currentTarget.dataset.index)
			var totalPrice=0;
			var checkLen=0;
			this.total_num=0;
			var arr=this.productList;
			arr[e.currentTarget.dataset.index].check=!arr[e.currentTarget.dataset.index].check
			this.rec_id_str=''
			for(var i=0;i<arr.length;i++){
				if(arr[i].check){
					checkLen++
					totalPrice += arr[i].goods_number*arr[i].shop_price
					if(checkLen>1){
						this.rec_id_str=this.rec_id_str+','+arr[i].rec_id
					}else{
						this.rec_id_str=this.rec_id_str+arr[i].rec_id+''
					}
					this.total_num+=arr[i].goods_number
				}
			}
			this.totalPrice=totalPrice
			if(checkLen==arr.length){
				this.allChecked=true
			}else{
				this.allChecked=false
			}
			// uni.setStorage({
			// 	key:"cart",
			// 	data:JSON.stringify(arr)
			// })
		},
		// 全选按钮
		allCheck(){
			var totalPrice=0;
			this.allChecked=!this.allChecked
			var arr=this.productList
			this.rec_id_str=''
			this.total_num=0
			if(this.allChecked){
				for(var i=0;i<arr.length;i++){
					this.total_num+=arr[i].goods_number
					totalPrice+=arr[i].goods_number*arr[i].shop_price
					if(totalPrice>0){
						this.rec_id_str=this.rec_id_str+','+arr[i].rec_id
					}
				}
				this.rec_id_str=this.rec_id_str.substring(1,this.rec_id_str.length)
				this.totalPrice=totalPrice
				
			}else{
				this.rec_id_str=''
				totalPrice=0
				this.total_num=0
				this.totalPrice=totalPrice
			}
			for(var i=0;i<arr.length;i++){
				arr[i].check=this.allChecked
			}
		},
		// 加号按钮
		add(e){
			var arr=this.productList;
			arr[e.currentTarget.dataset.index].goods_number++
			// console.log(arr[e.currentTarget.dataset.index].goods_number,arr[e.currentTarget.dataset.index].rec_id)
			var that=this
			uni.request({
				url:baseUrl+'cart/updCartGoods',
				method:"POST",
				data:{
					rec_id:arr[e.currentTarget.dataset.index].rec_id,
					goods_number:arr[e.currentTarget.dataset.index].goods_number
				}
			})
			// console.log(arr)
		},
		// 减号按钮
		cutBack(e){
			var arr=this.productList;
			arr[e.currentTarget.dataset.index].goods_number--
			if(arr[e.currentTarget.dataset.index].goods_number<=1){
				arr[e.currentTarget.dataset.index].goods_number=1
			}
			var that=this
			uni.request({
				url:baseUrl+'cart/updCartGoods',
				method:"POST",
				data:{
					rec_id:arr[e.currentTarget.dataset.index].rec_id,
					goods_number:arr[e.currentTarget.dataset.index].goods_number
				}
			})
		},
		// 删除商品
		remove(e){
			var id=this.productList[e.currentTarget.dataset.index].rec_id
			wx.showModal({
				title: '提示',
				content: "确定删除吗？",
				success: res => {
					if (res.confirm) {
						console.log(res.confirm)
					wx.showToast({
						title: '删除成功',
						icon: 'success',
						duration: 2000
					})
					  //删除逻辑
					var arr=this.productList;
					arr.splice(e.currentTarget.dataset.index,1)
					var totalPrice=0;
					for(var i=0;i<this.productList.length;i++){
						if(this.productList[i].check){
							totalPrice += this.productList[i].goods_number*this.productList[i].shop_price
						}
					}
					this.totalPrice=totalPrice
					uni.request({
						url:baseUrl+'cart/delCartGoods',
						method:"POST",
						data:{
							rec_id:id
						}
					})
					} else if (res.cancel) {
						console.log("取消")
					}
				}
			})
		},
		// 去结算按钮
		settlement(){
			var obj={
				rec_id_str:this.rec_id_str,
				total_num:this.total_num,
				total_price:this.totalPrice,
				goods_id:''
			}
			if(obj.total_price>0){
				wx.navigateTo({
					url:'/pages/settlement/settlement?operations=1&obj='+JSON.stringify(obj)
				})
			}else{
				wx.showToast({
				    title: '请选择商品',//提示文字
					icon:'none',
				    duration:1000//显示时长
				})
			}
			
		}
	},
	onShow(){
		this.allChecked=false
		var that=this
		uni.request({
			url:baseUrl+'cart/getCartList',
			method:'POST',
			success(e){
				// console.log(e.data.data.data.goods)
				that.productList=e.data.data.data.goods
			}
		})
	}
}
